import React, { useState, useEffect } from 'react';
import MenuItem_37 from '../components/MenuItem_37'
import './Directory_37.scss'
// import items from './menu-item-data'
import axios from 'axios'


const Directory2_37 = () => {
    const [menuItems, setMenuItems] =useState([]);

    useEffect(() => {
      const fetchMenuItems = async() => {
        const HEROKU_URL = 'https://crown1101.herokuapp.com/api_xx/category_xx'
        const {data} = await axios.get(HEROKU_URL)
        console.log('/api/category_xx', data)
        setMenuItems(data)
      }
      fetchMenuItems()
    }, [])

    console.log('menuItems', menuItems);
    return (
        <div className="directory-menu">
          { menuItems.map( item => {
              const {id, name, remote_url, size} = item;
              return(
                <MenuItem_37                  
                  key={id}
                  name={name}
                  remote_url={remote_url}
                  size={size}
                />
              )
          })
          
          }
       </div>
    )
}

export default Directory2_37;
