import React from 'react';
import { Link } from 'react-router-dom'
import { ReactComponent as Logo } from '../assets/crown.svg'
import './Header_37.scss'

const Header_37 = () => {
    return (
        <div >
            <div className="header">
                <Link to="/1101-208418037-CrownthemeReact" className="logo-container">
                <Logo />
                </Link>
                <div className="options">
                    <Link to="./Shop_37" className="option">Shop_37</Link>
                    <Link to="./ContactPage" className="option">Contact</Link>
                    <Link to="./SigninPage" className="option">Sign In</Link>
                </div>
            </div>
        </div>
     
    )
}

export default Header_37 ; 